




















































































/**
 * 회원 정보(프로필) 보기 또는 수정 페이지
 *
 * 서버에서 새로운 정보를 가져와서 보여준다.
 * - 최신 정보를 보여주기도 하고,
 * - 사용자 정보를 FORM 데이터 컨테이너로 그대로 사용하기 때문.
 *
 */
import Vue from "vue";
import Component from "vue-class-component";
import LoginFirst from "@/x-vue/components/user/LoginFirst.vue";
import { ApiService } from "@/x-vue/services/api.service";
import { RequestData, UserModel } from "@/x-vue/interfaces/interfaces";
import UploadImage from "@/x-vue/components/file/UploadImage.vue";
import store from "@/store";
import { AppService } from "@/service/app.service";
import UserPointProgressBar from "@/x-vue/components/user/UserPointProgressBar.vue";

@Component({
  components: {
    LoginFirst,
    UploadImage,
    UserPointProgressBar,
  },
})
export default class Profile extends Vue {
  app = AppService.instance;
  api: ApiService = ApiService.instance;

  progress = 0;
  nickname = "";

  get user(): UserModel {
    return store.state.user;
  }

  get changeable(): boolean {
    return store.state.user.nickname == "";
  }

  onProgress(p: number): void {
    this.progress = p;
  }

  async mounted(): Promise<void> {
    try {
      store.commit("user", await this.api.userProfile());
    } catch (e) {
      this.$emit("error", e);
    }
  }

  async profilePhotoChanged(): Promise<void> {
    try {
      store.commit("user", await this.api.userProfile());
    } catch (e) {
      this.app.error(e);
    }
  }

  async onSubmit(): Promise<void> {
    let form: RequestData = {
      nickname: this.nickname,
    };

    try {
      await this.api.profileUpdate(form);
      this.app.alert("profile", "profile_updated");
    } catch (e) {
      this.app.error(e);
    }
  }
}
